import React, { useState, useEffect } from "react";
import { Button, Card, Col, Row } from "react-bootstrap";
import IMAGES from "../../../assets/images";
import { useForm } from "react-hook-form";
import { Link, useLocation } from "react-router-dom";
import { PATH } from "../../../utils/path";
import { BsArrowLeftShort } from "react-icons/bs";
import { useDispatch, useSelector } from "react-redux";
import { confirmOtp, resendOtp } from "../../../app/features/auth/auth.slice";
import { ClipLoader } from "react-spinners";
import { requestPermission } from "../../../firebase";

export default function OtpCode() {
  const [otp, setOtp] = useState(["", "", "", ""]);
  const [seconds, setSeconds] = useState(60);
  const [intervalId, setIntervalId] = useState(null);
  const { isLoading } = useSelector((state) => state.auth);
  function handleChange(event, index) {
    const value = event.target.value;
    const newOtp = [...otp];
    newOtp[index] = value.replace(/\D/, "");
    setOtp(newOtp);
    if (index < 5 && value) {
      document.getElementById(`otp${index + 1}`)?.focus();
    }
  }

  function handleKeyDown(event, index) {
    if (event.key === "Backspace" && !otp[index] && index > 0) {
      document.getElementById(`otp${index - 1}`)?.focus();
    }
  }

  const { handleSubmit, register } = useForm();

  useEffect(() => {
    const id = setInterval(() => {
      setSeconds((prevSeconds) => prevSeconds - 1);
    }, 1000);
    setIntervalId(id);
    return () => clearInterval(id);
  }, []);

  useEffect(() => {
    if (seconds === 0) {
      // eslint-disable-next-line
      clearInterval(intervalId);
    }
  }, [seconds, intervalId]);

  const location = useLocation();
  const dispatch = useDispatch();

  const moveToNext = (data) => {
    localStorage.setItem("family_doc_app", JSON.stringify(data.data));
  };

  // const firebaseToken =  requestPermission();
  // console.log("firebaseToken", firebaseToken)
//   function requestFirebaseToken() {
//     return new Promise((resolve, reject) => {
//         // Simulate the requestPermission() function
//         setTimeout(() => {
//             const success = true; // Replace this with the actual result of requestPermission()

//             if (success) {
//                 const firebaseToken = requestPermission(); // Replace with the actual token
//                 resolve(firebaseToken); // Resolve the Promise with the token
//             } else {
//                 reject(new Error('Permission request failed')); // Reject the Promise with an error
//             }
//         }, 1000); // Simulate a delay
//     });
// }

// // Usage
// requestFirebaseToken()
//     .then(token => {
//         console.log('Firebase token:', token);
//         // You can now use the token to make API calls or perform other actions
//     })
//     .catch(error => {
//         console.error('Error:', error.message);
//         // Handle the error, such as showing an error message to the user
//     });

  // const onSubmit = (data) => {
  //   const concatenatedOtp = data.otp0 + data.otp1 + data.otp2 + data.otp3;
  //   requestPermission().then((currentToken) => {
  //     if (currentToken) {
  //       const confirmOtpData = {
  //         email: location?.state,
  //         otp: concatenatedOtp,
  //         timeZone: "Asia/Karachi",
  //         isLogin: true,
  //         deviceTypeId: 1,
  //         deviceId: currentToken,
  //       };
  //       dispatch(confirmOtp({ confirmOtpData, moveToNext }));
  //   }})
  // };
  const onSubmit = (data) => {
    const concatenatedOtp = data.otp0 + data.otp1 + data.otp2 + data.otp3;
  
    requestPermission().then((currentToken) => {
      if (currentToken) {
        const confirmOtpData = {
          email: location?.state, 
          otp: concatenatedOtp,
          timeZone: "Asia/Karachi",
          isLogin: true,
          deviceTypeId: 1,
          deviceId: currentToken,
        };
          dispatch(confirmOtp({ confirmOtpData, moveToNext }));
      } else {
        const confirmOtpData = {
          email: location?.state, 
          otp: concatenatedOtp,
          timeZone: "Asia/Karachi",
          isLogin: true,
          deviceTypeId: 1,
        };
          dispatch(confirmOtp({ confirmOtpData, moveToNext }));
        console.error("Permission not granted or token not available");
      }
    }).catch((error) => {
      console.error("Error in onSubmit:", error);
    });
  };

  const handleResendOtp = () => {
    setSeconds(60);
    const id = setInterval(() => {
      setSeconds((prevSeconds) => prevSeconds - 1);
    }, 1000);
    setIntervalId(id);

    const resendOtpData = {
      email: location.state,
    };

    dispatch(resendOtp({ resendOtpData }));
  };
  return (
    <div className="signin-component h-100">
      <Row className="m-0 vh-100">
        <Col sm={12} md={12} lg={7} className="p-0">
          <div className="bg-img">
            <div className="signin-page">
              <div
                style={{
                  padding: "22% 44% 22% 9%",
                }}
              >
                {/* <img src={IMAGES.LOGO} alt="HEAlogo" className="img-fluid" /> */}
                <h4 className="heading1">GP care with a Modern Touch.</h4>

                <p className="heading2">
                  Fast, hassle-free healthcare for the most common medical
                  conditions from the comfort of your home.
                </p>
              </div>
            </div>
          </div>
        </Col>
        <Col
          sm={12}
          md={12}
          lg={5}
          className="d-flex align-items-center py-4 h-100 bg-white"
        >
          <div className="signin-content">
            <div className="d-flex justify-content-center w-100 my-4">
              <img src={IMAGES.LOGO} alt="FAM Doc Logo" className="img-fluid" />
            </div>
            <Card className="border-0 bg-transparent">
              <Card.Body>
                <form className="p-3" onSubmit={handleSubmit(onSubmit)}>
                  <div className="signin-heading text-center">OTP Code</div>
                  <p className="text-center mb-5">
                    Please enter the code below we’ve sent to your email
                  </p>
                  {otp.map((digit, index) => (
                    <input
                      key={index}
                      type="text"
                      id={`otp${index}`}
                      name={`otp${index}`}
                      maxLength={1}
                      value={digit}
                      {...register(`otp${index}`, { required: true })}
                      onChange={(event) => handleChange(event, index)}
                      onKeyDown={(event) => handleKeyDown(event, index)}
                      className="otp-field me-3 py-1 text-center"
                      style={{
                        width: "20%",
                        fontweight: 500,
                        fontSize: "31px",
                      }}
                    />
                  ))}
                  <p className="text-center pt-4 text-black">
                    {seconds === 0 ? (
                      <span
                        onClick={handleResendOtp}
                        className="text-gray text-cursor-pointer"
                      >
                        Resend
                      </span>
                    ) : (
                      <span className="text-gray text-cursor-pointer" disabled>
                        Resend
                      </span>
                    )}{" "}
                    OTP in {seconds} Sec
                  </p>
                  <Button
                    className={`w-100 mt-5 ${isLoading && "disabled"}`}
                    type="submit"
                  >
                    {isLoading === true ? (
                      <ClipLoader
                        color="white"
                        size={25}
                        className="d-flex m-auto"
                      />
                    ) : (
                      "Confirm Code"
                    )}
                  </Button>
                  <div className="text-center pt-4">
                    <Link to={PATH.SIGNIN} className="back-sign-in">
                      <BsArrowLeftShort
                        className="align-items-center arrow-icon"
                        size={25}
                      />
                      Back To Sign In
                    </Link>
                  </div>
                </form>
              </Card.Body>
            </Card>
          </div>
        </Col>
      </Row>
    </div>
  );
}
